import { AfterViewInit, Component, EventEmitter, Input, Output } from '@angular/core';
import { BcCardAuthenticationBehavior } from '@bancolombia/design-system-behaviors';

@Component({
  selector: 'bc-card-authentication',
  templateUrl: 'bc-card-authentication.component.html',
})
export class BcCardAuthenticationComponent implements AfterViewInit {

  /**
   * Parámetro de entrada para el contenido del título.
   * 
   * @type {string}
   * @member BcCardAuthenticationComponent
   */
  @Input() titleCardAuth: string | null = null;

  /**
   * Parámetro de entrada para el contenido de la  descripción.
   * 
   * @type {string}
   * @member BcCardAuthenticationComponent
   */
  @Input() descriptionCardAuth!: string | null;

  /**
   * Parámetro de entrada para contenido del icono
   * 
   * @type {string}
   * @member BcCardAuthenticationComponent
   */
  @Input() iconCardAuth!: string | null;

  /**
   * Parámetro que permite eligir entre los dos tipos de variantes del card authentication.
   * @member BcCardAuthenticationComponent
   */
  @Input() typeContainerAuth: 'dynamicContainer' | 'staticContainer' = 'staticContainer';

  /**
   * Parámetro que permite eligir entre la opcion de dos botones o un boton.
   * @member BcCardAuthenticationComponent
   */
  @Input() amountOfButtons: 'one' | 'two' = 'one';

  /**
   * Salida de los valores que el usuario ingreso en los inputs
   * @member BcCardAuthenticationComponent
   */
  @Output() onSubmit = new EventEmitter<any>();

  /**
   * variable que hace referencia al comportamiento nativo del componente
   * @member BcCardAuthenticationComponent
   */
  BcCardAuthBehavior: BcCardAuthenticationBehavior;
  constructor() {

  }
  ngAfterViewInit(): void {
    this.BcCardAuthBehavior = new BcCardAuthenticationBehavior();
    this.BcCardAuthBehavior.setId('card1')
    this.BcCardAuthBehavior.setUp();
    this.BcCardAuthBehavior.outputFunction = (data, state) => {
      this.onSubmit.emit({ data, state });
    }
  }
}
